
  import { defineComponent } from 'vue';

  export default defineComponent({
    name: 'FortuneMachine',

    data() {
      return {
        zoom: {
          animation: false,
          timing: 1200,
        },
        machine: {
          started: false,
          timing: 1000,
        },
      }
    },

    methods: {
      zoomMachine() {
        if (this.zoom.animation) return;

        this.zoom.animation = true;
        setTimeout(() => {
          this.startMachine();
        }, this.zoom.timing);
      },

      startMachine() {
        if (this.machine.started) return;

        this.machine.started = true;
      }
    },
  });
