
  import { defineComponent } from 'vue';
  import { mapGetters } from 'vuex';

  export default defineComponent({
    name: 'FortuneCard',

    computed: {
      ...mapGetters('fortune_teller/fortune', {
        fortune: 'getFortune',
        fortuneError: 'getFortuneError',
        fortuneTranslated: 'getFortuneTranslated',
      }),
    },
  });
